import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { Router } from "@angular/router";
import {
  CorseCardCategoryM,
  DiplomaUserModel,
} from "app/home/models/home-course";
import { AppAssets } from "app/shared/data/app-assets";
import { Rate, Rates } from "app/shared/data/rate-list";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";
import { IAddEditCourseToCart } from "app/student/models/shopping-cart";
import { ShoppingCartService } from "app/student/services/shopping-cart.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-diploma-card",
  templateUrl: "./diploma-card.component.html",
  styleUrls: ["./diploma-card.component.scss"],
})
export class DiplomaCardComponent implements OnInit {
  @Input() Diploma: DiplomaUserModel;
  @Output() addedToCard = new EventEmitter<number>();
  rates: Rate[] = [];
  isStudent: boolean = false;
  isAdmin: boolean = false;

  constructor(
    public fileService: UploadFileService,
    public appAssets: AppAssets,
    public languageService: LanguageService,
    private authService: AuthService,
    private router: Router,
    private spinner: NgxSpinnerService,
    private shoppingCartService: ShoppingCartService,
    private msg: MessagesService
  ) {
    this.rates = Rates;
    this.isStudent = this.authService.isStudent();
    this.isAdmin = this.authService.isAdmin();
  }

  ngOnInit(): void {}

  getCategoresTooltip(categoryList: CorseCardCategoryM[]) {
    let Category: String[] = [];
    categoryList.forEach((ele, index) => {
      if (index != 0)
        Category.push(
          this.languageService.isArabic ? ele.ar_name : ele.en_name
        );
    });
    return Category.join("\n");
  }

  addToCart(diplomaId: number) {
    if (!this.authService.isUserLoggedIn()) {
      this.router.navigateByUrl("auth/login");
      return;
    }

    let cartData: IAddEditCourseToCart = {
      id: 0,
      diploma_id: diplomaId,
      subject_id: null,
    };

    this.spinner.show();
    this.shoppingCartService
      .addEditUserCartCourses(cartData)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.msg.success("course.AddToCartSuccess");
          // change add to cart into in cart
          this.addedToCard.emit(diplomaId);
        } else this.msg.warning(res.returnString);
        this.spinner.hide();
      });
  }
}
