import { Component, OnInit } from "@angular/core";
import { DiplomaUserModel } from "app/home/models/home-course";
import { HomeCoursesService } from "app/home/services/home-courses.service";
import { Storage } from "app/shared/services/local-storage";

@Component({
  selector: "app-diploma-home",
  templateUrl: "./diploma-home.component.html",
  styleUrls: ["./diploma-home.component.scss"],
})
export class DiplomaHomeComponent implements OnInit {
  Diplomas: DiplomaUserModel[] = [];
  curPage: number = 1;
  countItems: number = 0;

  constructor(private homeCoursesService: HomeCoursesService) {}

  ngOnInit(): void {
    this.getAllDiplomas();
  }

  getAllDiplomas() {
    this.homeCoursesService
      .getAllDiplomas(this.curPage, Storage.getUserId())
      .subscribe((res) => {
        this.Diplomas = res.DiplomaList;
        this.countItems = res.countItems;
      });
  }

  addedToCard(diplomaId: number) {
    this.Diplomas.map((course) => {
      if (course.id == diplomaId) course.isCard = true;
    });
  }
}
