import {
  DiplomaAndCoursesByCategoryResponse,
  DiplomaSearchResponse,
  DiplomaUserModel,
  DiplomaUserResponse,
  SearchCoursesInDiplomaRequest,
} from "./../models/home-course";
import {
  CourseCategoriesWithCourses,
  ICourseDataResponse,
  ICourseModel,
} from "app/home/models/home-course";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { SearchCoursesRequest } from "../models/home-course";

@Injectable({
  providedIn: "root",
})
export class HomeCoursesService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  // categories
  getAllCategoriesWithCourses() {
    return this.get<{ courseCategoryList: CourseCategoriesWithCourses[] }>(
      `courseCategory/getAllSubjectCategories`
    );
  }

  // get all courses by category Id
  getAllCoursesAndDiplomasByCategoryId(id: number, userId: number) {
    return this.get<DiplomaAndCoursesByCategoryResponse>(
      `subjectCategory/getAllCoursesAndDiplomasByCategoryId/${id}/${userId}`
    );
  }

  // search courses
  searchCourses(obj: SearchCoursesRequest, userId: number) {
    return this.post(`subjectCategory/searchCourses/${userId}`, obj);
  }

  // get course data and content
  getCourseDataById(id: number, userId: number) {
    return this.get<ICourseDataResponse>(
      `SubCourse/getSubCourseBySubCourseId/${id}/${userId}`
    );
  }

  // get all Popular Courses
  getMostPopularCourses(page: number, userId: number) {
    return this.get(`subjectCategory/getMostPopularCourses/${page}/${userId}`);
  }

  // get Recent Courses
  getMostRecentCourses(page: number, userId: number) {
    return this.get(`subjectCategory/getMostRecentCourses/${page}/${userId}`);
  }

  // get all categories
  getAllCategories(page: number) {
    //return 8 item
    return this.get(`courseCategory/returnAllCategoriesByPage/${page}`);
  }

  // get all diplomas
  getAllDiplomas(page: number, userId: number) {
    // page = null > return all
    return this.get<DiplomaUserResponse>(
      `Course/getAllDiploma/${page}/${userId}`
    );
  }

  searchDiplomaCourses(obj: SearchCoursesInDiplomaRequest, userId: number) {
    return this.post<DiplomaSearchResponse>(
      `Diploma/SearchDiploma/${userId}`,
      obj
    );
  }
}
