import { HomeIndexComponent } from "./pages/home-index/home-index.component";
import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { SearchCoursesComponent } from "./pages/home-courses/search-courses/search-courses.component";
import { CourseInfoComponent } from "./pages/home-courses/course-info/course-info.component";
import { MostPopularCourseIndexComponent } from "./pages/home-courses/popular-courses/most-popular-course-index/most-popular-course-index.component";
import { MostRecentCourseIndexComponent } from "./pages/home-courses/recent-courses/most-recent-course-index/most-recent-course-index.component";
import { DiplomaIndexComponent } from "./pages/home-courses/diploma/diploma-index/diploma-index.component";
import { DiplomaCoursesComponent } from "./pages/home-courses/diploma/diploma-courses/diploma-courses.component";

const routes: Routes = [
  {
    path: "",
    component: HomeIndexComponent,
    data: {
      breadcrumb: "Navbar.Home",
      view: "",
    },
  },
  {
    path: "search-courses/:categoryId",
    component: SearchCoursesComponent,
    data: {
      breadcrumb: "course.SearchOnCourses",
      view: "search-courses",
    },
  },
  {
    path: "course-info/:courseId",
    component: CourseInfoComponent,
    data: {
      breadcrumb: "course.courseInfo",
      view: "course-info",
    },
  },
  {
    path: "most-popular-course-index",
    component: MostPopularCourseIndexComponent,
    data: {
      breadcrumb: "course.PopularCourses",
      view: "most-popular-course-index",
    },
  },
  {
    path: "most-recent-course-index",
    component: MostRecentCourseIndexComponent,
    data: {
      breadcrumb: "course.RecentCourses",
      view: "most-recent-course-index",
    },
  },
  {
    path: "diploma-index",
    component: DiplomaIndexComponent,
    data: {
      breadcrumb: "course.Diplomas",
      view: "diploma-index",
    },
  },
  {
    path: "diploma-courses/:diplomaId",
    component: DiplomaCoursesComponent,
    data: {
      breadcrumb: "course.DiplomaCourse",
      view: "diploma-courses",
    },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class HomeRoutingModule {}
