import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import {
  DiplomaUserModel,
  ICourseModel,
  SearchCoursesRequest,
} from "app/home/models/home-course";
import { AppAssets } from "app/shared/data/app-assets";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { CategoriesService } from "app/admin/services/setup/categories.service";
import { Location } from "@angular/common";
import { AppStrings } from "app/shared/data/app-strings";
import { HomeCoursesService } from "app/home/services/home-courses.service";
import { Category } from "app/admin/models/setup/categories";
import { Storage } from "app/shared/services/local-storage";

@Component({
  selector: "app-search-courses",
  templateUrl: "./search-courses.component.html",
  styleUrls: ["./search-courses.component.scss"],
})
export class SearchCoursesComponent implements OnInit {
  myForm: FormGroup;
  courses: ICourseModel[] = [];
  diplomas: DiplomaUserModel[] = [];
  selectedCategory: Category = null;
  lastSearchName: string = null;
  categories: Category[] = [];

  constructor(
    public fileService: UploadFileService,
    public appAssets: AppAssets,
    public languageService: LanguageService,
    private route: ActivatedRoute,
    private homeCoursesService: HomeCoursesService,
    private categoriesService: CategoriesService,
    private fb: FormBuilder,
    private spinner: NgxSpinnerService,
    public authService: AuthService,
    public msg: MessagesService,
    private location: Location,
    private router: Router,
    public appStrings: AppStrings
  ) {}

  ngOnInit(): void {
    this.myForm = this.fb.group({
      categoryId: [0, Validators.required],
      filterCategory: ["" /* , Validators.required */],
      courseName: [""],
    });
    this.getAllCategories();

    // this.FormCtrls.courseName.setValue(
    //   this.router.getCurrentNavigation().extras.state?.courseName
    // );

    // categoryId = 0 then search in all // categoryId > 0 then search in selected category
    this.route.paramMap.subscribe((params) => {
      this.FormCtrls.categoryId.setValue(Number(params.get("categoryId")));
    });

    this.route.queryParamMap.subscribe((queryParams) => {
      this.FormCtrls.courseName.setValue(queryParams.get("courseName") ?? "");
      this.checkSerachType();
    });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  getAllCategories() {
    this.categoriesService.getCategoriesDropdown().subscribe((res: any) => {
      if (res) {
        this.categories = res.courseCategoryList;
      }
    });
  }

  getSelectedCategoryFormList() {
    let index = this.categories.findIndex(
      (category) => category.id == this.FormCtrls.categoryId.value
    );
    this.selectedCategory = index >= 0 ? this.categories[index] : null;
  }

  getAllCoursesInCategory() {
    this.courses = [];
    this.diplomas = [];
    this.lastSearchName = null;
    this.spinner.show();
    this.homeCoursesService
      .getAllCoursesAndDiplomasByCategoryId(
        this.FormCtrls.categoryId.value,
        Storage.getUserId()
      )
      .subscribe((res) => {
        if (res) {
          this.courses = res.subCategoryList;
          this.diplomas = res.diplomaCategoryList;
          this.location.go(
            `home/search-courses/${this.FormCtrls.categoryId.value}`
          );
          this.getSelectedCategoryFormList();
        }
        this.spinner.hide();
      });
  }

  checkSerachType() {
    if (this.FormCtrls.categoryId.value > 0 && !this.FormCtrls.courseName.value)
      this.getAllCoursesInCategory();
    else this.searchCourses();
  }

  searchCourses() {
    if (this.myForm.valid) {
      let courseSearchData: SearchCoursesRequest = {
        courseCategory_id: this.FormCtrls.categoryId.value,
        name: this.FormCtrls.courseName.value.trim().toLowerCase(),
      };

      this.spinner.show();
      this.homeCoursesService
        .searchCourses(courseSearchData, Storage.getUserId())
        .subscribe((res: any) => {
          this.courses = [];
          this.diplomas = [];
          this.lastSearchName = courseSearchData.name;
          if (res) {
            this.courses = res.subCategoryList;
            this.diplomas = res.diplomaList;
            this.location.go(
              `home/search-courses/${courseSearchData.courseCategory_id}?courseName=${courseSearchData.name}`
            );
            if (courseSearchData.courseCategory_id > 0)
              this.getSelectedCategoryFormList();
          }
          this.spinner.hide();
        });
    } else this.msg.warning("course.enterCourseNameField");
  }

  addedToCard(id: number) {
    this.courses.map((course) => {
      if (course.subject_id == id) course.isCard = true;
    });
    this.diplomas.map((diploma) => {
      if (diploma.id == id) diploma.isCard = true;
    });
  }

  removeFromCard(course) {
    // let i = this.courses.indexOf(course);
    // this.courses.splice(i, 1);
    // this.msg.success("General.DeleteSuccessfully");
    this.courses.map((course) => {
      if (course.subject_id == course.subject_id) course.isCard = false;
    });
  }

  onChange($event) {
    if (!$event.value) {
      this.selectedCategory = null;
    }
  }
}
