import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import {
  DiplomaUserModel,
  ICourseModel,
  SearchCoursesInDiplomaRequest,
} from "app/home/models/home-course";
import { AppAssets } from "app/shared/data/app-assets";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";
import { NgxSpinnerService } from "ngx-spinner";
import { Location } from "@angular/common";
import { HomeCoursesService } from "app/home/services/home-courses.service";
import { Storage } from "app/shared/services/local-storage";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { Rate, Rates } from "app/shared/data/rate-list";
import { CheckUserBuyCoursesDialogComponent } from "app/student/pages/check-user-buy-courses-dialog/check-user-buy-courses-dialog.component";
import { MatDialog } from "@angular/material/dialog";
import { AuthService } from "app/shared/services/auth/auth.service";
import { ShoppingCartService } from "app/student/services/shopping-cart.service";
import { IAddEditCourseToCart } from "app/student/models/shopping-cart";

@Component({
  selector: "app-diploma-courses",
  templateUrl: "./diploma-courses.component.html",
  styleUrls: ["./diploma-courses.component.scss"],
})
export class DiplomaCoursesComponent implements OnInit {
  myForm: FormGroup;
  courses: ICourseModel[] = [];
  selectedDiploma: DiplomaUserModel = null;
  lastSearchName: string = null;
  rates: Rate[] = [];

  constructor(
    public fileService: UploadFileService,
    public appAssets: AppAssets,
    public languageService: LanguageService,
    private route: ActivatedRoute,
    private homeCoursesService: HomeCoursesService,
    private fb: FormBuilder,
    private spinner: NgxSpinnerService,
    public msg: MessagesService,
    private location: Location,
    private courseService: CourseService,
    private dialog: MatDialog,
    private authService: AuthService,
    private router: Router,
    private shoppingCartService: ShoppingCartService
  ) {
    this.rates = Rates;
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      diplomaId: [0, Validators.required],
      courseName: [""],
    });

    this.route.paramMap.subscribe((params) => {
      this.FormCtrls.diplomaId.setValue(Number(params.get("diplomaId")));
    });

    this.route.queryParamMap.subscribe((queryParams) => {
      this.FormCtrls.courseName.setValue(queryParams.get("courseName") ?? "");
      this.searchCourses();
    });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  searchCourses() {
    // console.log("by course");
    if (this.FormCtrls.diplomaId.value) {
      let courseSearchData: SearchCoursesInDiplomaRequest = {
        diplomaId: this.FormCtrls.diplomaId.value,
        courseName: this.FormCtrls.courseName.value.trim().toLowerCase(),
      };

      this.spinner.show();
      this.homeCoursesService
        .searchDiplomaCourses(courseSearchData, Storage.getUserId())
        .subscribe((res) => {
          this.courses = [];
          this.lastSearchName = courseSearchData.courseName;
          if (res) {
            this.selectedDiploma = res?.diploma;
            this.courses = res?.diploma_courses;
            this.courses.map((course) => {
              course.isPaid = this.selectedDiploma.isPaid;
            });
            // change url
            if (courseSearchData.courseName)
              this.location.go(
                `home/diploma-courses/${courseSearchData.diplomaId}?courseName=${courseSearchData.courseName}`
              );
            else
              this.location.go(
                `home/diploma-courses/${courseSearchData.diplomaId}`
              );
          }
          this.spinner.hide();
        });
    }
    // else this.msg.warning("course.enterCourseNameField");
  }

  addedToCard(courseId: number) {
    this.courses.map((course) => {
      if (course.subject_id == courseId) course.isCard = true;
    });
  }

  removeFromCard(course) {
    this.courses.map((course) => {
      if (course.subject_id == course.subject_id) course.isCard = false;
    });
  }

  // openCheckUserBuyCourseDialog(): void {
  //   if (!this.authService.isUserLoggedIn()) {
  //     this.router.navigateByUrl("auth/login");
  //     return;
  //   }

  //   this.dialog
  //     .open(CheckUserBuyCoursesDialogComponent, {
  //       data: {
  //         msg: "",
  //       },
  //     })
  //     .afterClosed()
  //     .subscribe((confirm) => {
  //       if (confirm) this.buyCourse();
  //     });
  // }

  // buyCourse() {
  //   let data: any;
  //   this.msg.success("course.buySuccesed");

  //   this.shoppingCartService.buyCourse(data).subscribe((res) => {
  //     if (res) {
  //       this.msg.success("course.buySuccesed");
  //     }
  //   });
  // }

  addToCart(diplomaId: number) {
    if (!this.authService.isUserLoggedIn()) {
      this.router.navigateByUrl("auth/login");
      return;
    }

    let cartData: IAddEditCourseToCart = {
      id: 0,
      diploma_id: diplomaId,
      subject_id: null,
    };

    this.spinner.show();
    this.shoppingCartService
      .addEditUserCartCourses(cartData)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.msg.success("course.AddToCartSuccess");
          // change add to cart into in cart
          this.selectedDiploma.isCard = true;
        } else this.msg.warning(res.returnString);
        this.spinner.hide();
      });
  }
}
