import { Component, EventEmitter, OnInit, Output } from "@angular/core";
import {
  ICourseModel,
  PopularCoursesResponse,
} from "app/home/models/home-course";
import { HomeCoursesService } from "app/home/services/home-courses.service";
import { customOptions } from "app/shared/data/owl-options";
import { Storage } from "app/shared/services/local-storage";
import { MessagesService } from "app/shared/services/messages.service";
import { OwlOptions } from "ngx-owl-carousel-o";

@Component({
  selector: "app-most-popular-courses",
  templateUrl: "./most-popular-courses.component.html",
  styleUrls: ["./most-popular-courses.component.scss"],
})
export class MostPopularCoursesComponent implements OnInit {
  courses: ICourseModel[] = [];
  @Output() PopularItemsCount = new EventEmitter<number>();
  options: OwlOptions = customOptions;
  countItems: number = 0;

  constructor(
    private homeCoursesService: HomeCoursesService,
    private msg: MessagesService
  ) {}

  ngOnInit(): void {
    this.getallPopularCourses();
  }

  getallPopularCourses() {
    this.homeCoursesService
      .getMostPopularCourses(1, Storage.getUserId())
      .subscribe((res: PopularCoursesResponse) => {
        res.mostPopularCourseList.forEach((ele: any) => {
          this.courses.push(ele.Course);
        });
        this.countItems = res.countItems;
        this.PopularItemsCount.emit(this.courses?.length);
      });
  }

  addedToCard(courseId) {
    this.courses.map((course) => {
      if (course.subject_id == courseId) course.isCard = true;
    });
  }
  removeFromCard(course) {
    let i = this.courses.indexOf(course);
    this.courses.splice(i, 1);
    this.msg.success("General.DeleteSuccessfully");
  }
}
