import { Component, OnInit } from "@angular/core";
import { Category } from "app/admin/models/setup/categories";
import { HomeCoursesService } from "app/home/services/home-courses.service";

@Component({
  selector: "app-course-categories",
  templateUrl: "./course-categories.component.html",
  styleUrls: ["./course-categories.component.scss"],
})
export class CourseCategoriesComponent implements OnInit {
  CourseCategories: Category[];
  curPage: number = 1;
  countItems: number = 0;


  constructor(private homeCoursesService: HomeCoursesService) {}

  ngOnInit(): void {
    this.getAllCategories();
  }

  getAllCategories() {
    this.homeCoursesService
      .getAllCategories(this.curPage)
      .subscribe((res: any) => {
        this.CourseCategories = res.courseCategoryList;
        this.countItems=res.countItems;

      });
  }

  moreCategories() {
    this.curPage++;
    this.homeCoursesService
      .getAllCategories(this.curPage)
      .subscribe((res: any) => {
        for(var i=0;i<res.courseCategoryList.length;i++){
          let category = {} as Category;

          category.id = res.courseCategoryList[i].id;
          category.ar_name =  res.courseCategoryList[i].ar_name;
          category.en_name =  res.courseCategoryList[i].en_name;
          category.attach_path =  res.courseCategoryList[i].attach_path;
          this.CourseCategories.push(category);

        }




      });
  }
}
