import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import {
  ICourseGetVM,
  IUnitDataGetVM,
} from "app/admin/models/admin/setup/course";
import { HomeCoursesService } from "app/home/services/home-courses.service";
import { AppAssets } from "app/shared/data/app-assets";
import { AppStrings } from "app/shared/data/app-strings";
import { Rate, Rates } from "app/shared/data/rate-list";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import {
  IAddEditCourseToCart,
  RatetCourseRequest,
  RatetCourseResponse,
} from "app/student/models/shopping-cart";
import { ShoppingCartService } from "app/student/services/shopping-cart.service";
import { NgxSpinnerService } from "ngx-spinner";
import { Storage } from "app/shared/services/local-storage";
import { CheckUserBuyCoursesDialogComponent } from "app/student/pages/check-user-buy-courses-dialog/check-user-buy-courses-dialog.component";
import { MatDialog } from "@angular/material/dialog";

@Component({
  selector: "app-course-info",
  templateUrl: "./course-info.component.html",
  styleUrls: ["./course-info.component.scss"],
})
export class CourseInfoComponent implements OnInit {
  rates: Rate[] = [];
  courseId: number;
  courseData: ICourseGetVM;
  courseContent: IUnitDataGetVM[];
  myForm: FormGroup;

  constructor(
    public fileService: UploadFileService,
    public appAssets: AppAssets,
    public languageService: LanguageService,
    private route: ActivatedRoute,
    private homeCoursesService: HomeCoursesService,
    private spinner: NgxSpinnerService,
    public msg: MessagesService,
    public appStrings: AppStrings,
    private router: Router,
    private fb: FormBuilder,
    private authService: AuthService,
    private shoppingCartService: ShoppingCartService,
    private dialog: MatDialog
  ) {
    this.rates = Rates;
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      courseNameSearch: [
        "",
        [Validators.required, CustomeValidator.whiteSpace],
      ],
    });

    this.route.paramMap.subscribe((params) => {
      this.courseId = Number(params.get("courseId"));
      this.getCourseData();
    });
    // console.log(this.courseId);
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  getCourseData() {
    this.spinner.show();
    this.homeCoursesService
      .getCourseDataById(this.courseId, Storage.getUserId())
      .subscribe((res) => {
        if (res) {
          // course data
          this.courseData = res.sub_course;
          this.courseData.course_Minutes = this.courseData.courseMinutes % 60;
          this.courseData.course_hours = Math.floor(
            this.courseData.courseMinutes / 60
          );

          // course content
          this.courseContent = res.Content;
          this.courseContent.map((unit) => {
            unit.unitContent = unit.exams.concat(unit.materials);
            unit.unitContent.sort((a, b) => a.order - b.order);
          });
          this.courseContent = this.courseContent.reverse();
          // console.log(this.courseContent);
        }
        this.spinner.hide();
      });
  }

  openCheckUserBuyCourseDialog(): void {
    if (!this.authService.isUserLoggedIn()) {
      this.router.navigateByUrl("auth/login");
      return;
    }

    this.dialog
      .open(CheckUserBuyCoursesDialogComponent, {
        data: {
          msg: "",
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.buyCourse();
      });
  }

  buyCourse() {
    let data: any;
    this.msg.success("course.buySuccesed");

    this.shoppingCartService.buyCourse(data).subscribe((res) => {
      if (res) {
        this.msg.success("course.buySuccesed");
      }
    });
  }

  addToCart(courseId: number) {
    if (!this.authService.isUserLoggedIn()) {
      this.router.navigateByUrl("auth/login");
      return;
    }

    let cartData: IAddEditCourseToCart = {
      id: 0,
      subject_id: courseId,
      diploma_id: null,
    };

    this.spinner.show();
    this.shoppingCartService
      .addEditUserCartCourses(cartData)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.msg.success("course.AddToCartSuccess");
          // change add to cart into in cart
          this.courseData.isCard = true;
        } else this.msg.warning(res.returnString);
        this.spinner.hide();
      });
  }

  searchCourses() {
    if (this.myForm.valid) {
      this.router.navigate([`home/search-courses/0`], {
        queryParams: { courseName: this.formCtrls.courseNameSearch.value },
      });
    } else this.msg.warning("course.enterCourseNameField");
  }
}
