import { Component, OnInit } from "@angular/core";
import { AppAssets } from "app/shared/data/app-assets";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { AuthService } from "app/shared/services/auth/auth.service";
import { MessagesService } from "app/shared/services/messages.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";

@Component({
  selector: "app-home-index",
  templateUrl: "./home-index.component.html",
  styleUrls: ["./home-index.component.scss"],
})
export class HomeIndexComponent implements OnInit {
  myForm: FormGroup;
  PopularItemsCount: number = 0;
  RecentItemsCount: number = 0;

  constructor(
    public authservice: AuthService,
    public appAssets: AppAssets,
    private fb: FormBuilder,
    public msg: MessagesService,
    private router: Router
  ) {}

  ngOnInit(): void {}

  searchCourses(courseName) {
    if (courseName) {
      this.router.navigate([`home/search-courses/0`], {
        queryParams: { courseName: courseName },
        state: { courseName: courseName },
      });
    } else this.msg.warning("course.enterCourseNameField");
  }
  getPoupularCount(event) {
    this.PopularItemsCount = event;
  }
  getRecentCount(event) {
    this.RecentItemsCount = event;
  }
}
